<!--  -->
<template>
  <div class=''>
    <p>{{names}}</p>
    <p v-for="item in names" :key="item" >{{item}}</p>
    <p>{{dynamic}}</p>
    <el-row v-if="dynamic">
      <el-button size='mini' type="primary" @click="receive">收到？</el-button>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: '',
    // 接收父组件传过来的值
    props: ["names", "dynamic"],
    // props:{"names":Array, "dynamic":String},
    

    data() {
      //这里存放数据
      return {
        sondata: '子组件收到'
      };
    }, 
    //方法集合
    methods: {
      receive() {
        // 子组件传数据给父组件 show 自定义方法 父组件用这个方法接收
        this.$emit('show', this.sondata)
      }
    },
  }

</script>
<style lang='' scoped>
  /* @import url(); 引入公共css类 */

</style>



